import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import * as serviceWorker from './serviceWorker';
import {BrowserRouter as Router,NavLink} from 'react-router-dom';
import Route from 'react-router-dom/Route';
import Home from './component/home/home';
import Wedo from './component/we-do/we-do';
import Events from "./component/our-events/events";
import Contact from './component/contact us/contact';


const Homepage=()=>{return( <div><Home/></div>);}
const Work=()=>{return( <h1><Wedo/></h1>);}
const Event=()=>{return( <h1><Events/></h1>);}
const contacts=()=>{return( <h1><Contact/></h1>);}



class App extends React.Component {
  render(){
  return (

    <Router>
<div className="App"> 
<div className="navbar">
  <NavLink  className="nav-item" to="/shiksha-work"  exact activeStyle={{color:'orange'}}>Home</NavLink>
<NavLink className="nav-item"  to="/about" exact activeStyle={{color:'orange'}}>About</NavLink>
<NavLink className="nav-item"  to="/event" exact activeStyle={{color:'orange'}}>Events</NavLink>
<NavLink className="nav-item"  to="/contact" exact activeStyle={{color:'orange'}}>Contact US</NavLink></div>

<Route path="/shiksha-work" exact strict render={Homepage}/>
<Route path="/about" exact strict render={Work}/>
<Route path="/event" exact strict render={Event}/>  
<Route path="/contact" exact strict render={contacts}/>  

    </div></Router>
  
  );}
}

export default App;





ReactDOM.render(<App />, document.getElementById('root'));
serviceWorker.unregister();